* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Space Grotesk', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  overflow: hidden;
}

.App {
  width: 100vw;
  height: 100vh;
  background: #000;
}

.info {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  color: #fff;
  z-index: 10;
  pointer-events: none;
}

.info h1 {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 10px;
  text-shadow: 0 0 20px rgba(0, 255, 136, 0.8);
}

.info p {
  font-size: 1rem;
  opacity: 0.8;
}

@media (max-width: 768px) {
  .info h1 {
    font-size: 1.8rem;
  }
  .info p {
    font-size: 0.9rem;
  }
}
